body{
    background-color : #f0f0f2;
    margin:0;
    padding:0;
    font-family : "Open Sans","Helvetica Neue",Arial, Helvetica, sans-serif;
}
div{
    width:600px;
    margin: 5em auto;
    padding : 50px;
    background-color: #fff;
    border-radius: 1em;
}
a:link,a:visited {
    color:#38488f;
    text-decoration :none;
}

@media (max-width: 700px){
    body{
        background-color: #fff;
    }
    div{
        width:auto;
        margin:0 auto;
        border-radius: 0;
        padding:1em;
    }
}